import { FC, ReactNode } from "react";

interface Item {
  title?: string;
  icon?: ReactNode;
  key: string | number;
}

export interface TabBarProps {
  items?: Item[];
  activeKey?: string | number;
  className?: string;
  onClick?: (key: string | number, lastKey?: string | number) => void;
}

const TabBar: FC<TabBarProps> = ({
  items,
  activeKey,
  className,
  onClick
}) => {
  return (
    <div className={`tab-bar ${className}`}>
    {
      items?.map(({key, title, icon}) => (
        <div
          key={key}
          aria-selected={activeKey === key}
          onClick={() => onClick?.(key, activeKey)}>
          {icon}
          {title}
        </div>
      ))
    }
    </div>
  );
};
export default TabBar;